<template>
  <div style="margin: 30px">
    <el-divider></el-divider>
      <div>
    <el-descriptions
      class="margin-top"
      title="模型版本信息"
      style="margin-top: 25px"
      :column="3"
      :size="size"
    >
      <template slot="extra">
        <router-link
          :to="{ name: '模型版本管理', params: {model_name: 
               this.$route.params.model_name}}"
          style="text-decoration: none; color: aliceblue"
          align="right">
          <el-button class=""
            ><i class="el-icon-arrow-left" style="margin-right: 10px"></i
            >返回模型版本管理</el-button
          >
        </router-link>
      </template>
    
      <el-descriptions-item label="模型名称">
          {{ ModelVersionbaseInfo.name}}
      </el-descriptions-item>
      <el-descriptions-item label="模型版本">
          {{ ModelVersionbaseInfo.version}}
      </el-descriptions-item>
      <el-descriptions-item label="创建时间">
          {{ ModelVersionbaseInfo.creation_timestamp | formatDate }}
      </el-descriptions-item>
      <el-descriptions-item label="最近修改时间">
          {{ ModelVersionbaseInfo.last_updated_timestamp | formatDate }}
      </el-descriptions-item>
      <el-descriptions-item label="Stage">
          <span>
          <el-dropdown @command="handleModelStage">
                    <span class="el-dropdown-link">
                        <el-tag size="small" :type='stage_status1'>{{ ModelVersionbaseInfo.current_stage}}</el-tag><i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="a">转换为&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-right"></i>
                            <el-tag size="small" :type="stage_status2">{{to_be_stage2}}</el-tag>
                        </el-dropdown-item>
                        <el-dropdown-item command="b">转换为&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-right"></i>
                            <el-tag size="small" :type="stage_status3">{{to_be_stage3}}</el-tag>
                        </el-dropdown-item>
                        <el-dropdown-item command="c">转换为&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-right"></i>
                            <el-tag size="small" :type="stage_status4">{{to_be_stage4}}</el-tag>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
          </span>
      </el-descriptions-item>
    </el-descriptions>

    <div class="text item">
        {{'Source Run：' + ModelVersionbaseInfo.run_id }}
    </div>
 </div>
    <br>
    <el-divider></el-divider>

     <el-collapse v-model="activeNames">
            <el-collapse-item title="描述" name="1">
                <div> 
                    <slot :description="description" class="main-a">
                    <span v-show="!editModelVersionVisible">{{ModelVersionbaseInfo.description || '无'}}</span>
                    <el-input v-model="editModelForm.description" type="textarea" 
                              :autosize="{ minRows: 4, maxRows: 6}"
                              class="modelDescStyle"
                              v-show="editModelVersionVisible">
                    </el-input>
                    <el-button  v-show="!editModelVersionVisible" 
                               class="inline" 
                               icon="el-icon-edit" 
                               type="text"  
                               @click="handleModelVersionDescEdit('index',{description:'description'})" 
                               style="padding: 0px; margin-right: 10px;">
                    </el-button>
                    <br>
                     <span v-show="editModelVersionVisible">
                        <el-button type="text"  @click.native="editModelVersionVisible = false" icon="el-icon-back">取消</el-button>
                        <el-button type="text" @click.native="editSubmit" :loading="editLoading" icon="el-icon-check">保存</el-button>
                    </span>
                   </slot>
                </div>
              </el-collapse-item>

       </el-collapse>
    <el-dialog
      title="Stage Transition"
      :visible.sync="StageTranVisible"
      width="30%">
        <span>Transition to &nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-right"></i>&nbsp;&nbsp;&nbsp;&nbsp;<el-tag size="small" :type="stage_status0">
            {{status_msg}}</el-tag></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="StageTranVisible = false">取 消</el-button>
          <el-button type="primary" @click="StageTranSubmit">确 定</el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>

import { test, getModelVersionBaseinfo, getModelVersions} from '@/api/api'
// eslint-disable-next-line no-unused-vars
import $ from 'jquery'

export default {
    data () {
        return {
            status_msg:'',
            StageTranVisible:false,
            to_be_stage2:'None',
            to_be_stage3:'None',
            to_be_stage4:'None',
            stage_status1:'info',
            stage_status2:'info',
            stage_status3:'info',
            stage_status4:'info',
            selsVersionCompare: [],
            versionList: [],
            stageStatus: 'All',
            editLoading:false,
            ModelVersionbaseInfo:{},
            editModelVersionVisible: false,
            activeNames: ['1'],
            sels:[],
            total: 0,
            page: 1,
            listLoading: false,
            editModelForm:{
                description: ''
            }
        }
    },
  created () {
    // 如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
    // this.form.Tasks = this.Tasks[0].id
  },
    filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    },
  methods: {
      StageTranSubmit () {
          let self = this
          let param = JSON.stringify({ model_name: this.$route.params.model_name,
                                      version_id: this.$route.params.version_id,
                                      stage: this.status_msg,
                                      user_key:JSON.parse(sessionStorage.getItem('name'))
                                     })
          console.log(param, 'dsdds')
          $.ajax({
              type: 'post',
              url: test + '/api/models/model_stage_transform',
              async: true,
              data: param,
              headers: {
                  'Content-Type': 'application/json',
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: (data) => {
                  self._getModelVersionBaseinfo()
                  this.StageTranVisible=false
              }
          })
      },
      handleModelStage(command) {
          this.StageTranVisible=true
          if(command==='a'){
              this.status_msg = this.to_be_stage2
          }else if(command==='b'){
              this.status_msg = this.to_be_stage3
          }else{
              this.status_msg = this.to_be_stage4
          }
          if(this.status_msg==='Production'){
              this.stage_status0='success'
          }else if(this.status_msg==='Staging'){
              this.stage_status0='warning'
          }else{
              this.stage_status0='info'
          }
      },
      // 获取模型版本列表
      getVersionList (stage_btn) {
          this.listLoading = true
          let self = this
          let params = {
              model_name: this.$route.params.model_name,
              page: self.page,
              stage_btn: stage_btn,
              user_key: JSON.parse(sessionStorage.getItem('name')),
          }
          let headers = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          }
          getModelVersions(headers, params).then(_data => {
              let { msg, code, data } = _data
              self.listLoading = false
              if (code === '999999') {
                  self.total = data.total
                  self.versionList = data.data
                  console.log(self.versionList,"self.versionList")
              } else {
                  self.$message.error({
                      message: msg,
                      center: true
                  })
              }
          })
      },
      clickBar: function(index){
          if (index==1){
              this.getVersionList('all')
          }else{
              this.getVersionList('active')
          }
      },
      // 显示编辑界面
      handleModelVersionDescEdit: function (index, row) {
          this.editModelVersionVisible = true
          let desc = this.ModelVersionbaseInfo.description
          // this.editForm = Object.assign({}, row);
          this.editModelForm = Object.assign({}, { description: desc })
    },
      disabledCheckBox(row, index) {
          if (row.is_dir===false) {
              return true
          } else {
              return false
          }
      },

      handleModelOps(command) {
          console.log(command,"command")
           this.$router.push(
               {
                   name: '模型即服务',
                   params:
                   {
                       project_id: this.$route.params.project_id,
                       task_id: this.$route.params.task_id,
                       model_id: command
                   }
               })
          this.$message('click on item ' + command);
          //"handleModelOps(scope.$index,scope.row)"
      },
    // 获取模型任务列表
    _getModelVersionBaseinfo () {
      this.listLoading = true
      let self = this
      let params = {
        name: this.$route.params.model_name,
        version: this.$route.params.version_id,
        user_key: JSON.parse(sessionStorage.getItem('name'))
      }

      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getModelVersionBaseinfo(headers, params).then(_data => {
        let { msg, code, data } = _data
        self.listLoading = false
        if (code === '999999') {
            self.ModelVersionbaseInfo = data
            if (self.ModelVersionbaseInfo.current_stage==='Production'){
                self.stage_status1="success"
                self.to_be_stage2 = "Staging"
                self.stage_status2 = "warning"
                self.to_be_stage3 = "None"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Archived"
                self.stage_status4 = "info"
            }else if(self.ModelVersionbaseInfo.current_stage==='Staging'){
                self.stage_status1="warning"
                self.to_be_stage2 = "Production"
                self.stage_status2 = "success"
                self.to_be_stage3 = "None"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Archived"
                self.stage_status4 = "info"
            }else if(self.ModelVersionbaseInfo.current_stage==='Archived') {
                self.stage_status1="info"
                self.to_be_stage2 = "Production"
                self.stage_status2 = "success"
                self.to_be_stage3 = "None"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Staging"
                self.stage_status4 = "warning"                
            }else{
                self.stage_status1="info"
                self.to_be_stage2 = "Production"
                self.stage_status2 = "success"
                self.to_be_stage3 = "Archived"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Staging"
                self.stage_status4 = "warning"  
            }
            console.log(self.ModelVersionbaseInfo,"ModelbaseInfo")
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    editSubmit: function(){
        let self = this
        this.editLoading= true
        let param = JSON.stringify({ 
            model_name: this.$route.params.model_name,
            version: this.$route.params.version_id,
            description: this.editModelForm.description,
            user_key: JSON.parse(sessionStorage.getItem('name'))
        })

      console.log(param, 'dsdds')
      $.ajax({
        type: 'POST',
        url: test + '/api/models/edit_model_version_description',
        async: true,
        data: param,
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
            // console.log(data,"dddddd")
            this.editLoading= false
            this.editModelVersionVisible = false
            this._getModelVersionBaseinfo()
            
        }
      })
        
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = {
          project_id: Number(this.$route.params.project_id),
          table_name: row.tableName,
          ids: [row.id]
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        console.log(params, 'paramsparamsparamslplp')
        delSampleTable(headers, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getSampleList()
        })
      })
    },

    handleCurrentChange (val) {
      this.page = val
      this.getVersionList('all')
    },
    selsChangeVersionTracking: function (sels) {
      this.selsVersionCompare = sels
    },
      //比较模型版本信息
      compareVersion: function () {
          let ids = this.selsVersionCompare.map(item => item.id)
          let self = this
          this.$confirm('确认比较选中记录吗？', '提示', {
              type: 'warning'
          }).then(() => {
              self.listLoading = true
              let params = {
                  project_id: Number(this.$route.params.project_id),
                  ids: ids
              }
              let headers = {
                  'Content-Type': 'application/json',
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              }
              this.$router.push(
                  {
                      name: '模型实验跟踪详情',
                      params:
                      {
                          project_id: this.$route.params.project_id,
                          task_id: this.$route.params.task_id,
                          exp_id: ids[0],
                      },
                      query:{exp_id_list: ids}
                  })
              // eslint-disable-next-line no-undef
          })
      },
  },
  mounted () {
      this._getModelVersionBaseinfo()
      
          
  }
}
</script>
